___CSS_LOADER_EXPORT___.push([module.id, `.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container-subsections {
    display: flex;
    padding: 10px 80px;
}

.ad-banner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-left: 100px;
    margin-right: 100px;
}

.sobre-nos-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
}

.sobre-nos-content {
    width: auto;
    display: flex;
    justify-content: center;
}

.sobre-nos-content p{
    font-size: 20px; 
    color: black;
}

.sobre-nos-content h1{
    text-align: center;
    font-size: 40px; 
    margin-top: 20px;
}

#container-home {
    display: flex;
    flex-direction: column;
    width: 100%;
    box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.1);
    margin-bottom: 50px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

#home {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 900px;
}

#home h1 {
    text-align:center; 
    font-size: 60px; 
    margin-top: 100px; 
    color: white
}

#home p {
    text-align:center; 
    font-size: 20px; 
    color: white;
}

#precos {
    justify-content: center; 
    margin-bottom: 50px; 
    margin-top: 175px;

}

.banner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 500px;
    width: 450px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.banner h1 {
    border-bottom: 2px solid black;
    text-align: center;
    font-size: 40px;
}

.banner p {
    color: #000;
    text-align: center;
    font-size: 20px;
}

.upper-banner h1 {
    font-size: 69px; 
    text-align: center;
}

.upper-banner p {
    font-size: 20px; 
    color: black;
}

.banner-items {
    display: flex;
}

.banner-items i {
    display: flex;
    align-items: center;
    color: #32BBFF;
    font-size: 20px;
    margin-right: 7px;
}

ltgrey {
    color: #858585;
    font-size: 16px;
}

@media (max-width: 1186px) {
   .sobre-nos-container img {
        width: 500px;
   }
}

@media (max-width: 1028px) {
   .sobre-nos-container img {
        display: none;
   }

   .container-subsections {
    flex-direction: column;
   }

   .down-sobre-nos {
    order: 2;
   }

   .upper-sobre-nos {
    order: 1;
   }

   .upper-banner {
    margin-bottom: 100px;
   }

   #precos {
    margin-top: 125px;
   }
}

@media (max-width: 700px) {
    #home {
        height: 700px;
    }

    #home h1 {
        font-size: 50px;
    }

    .sobre-nos-content h1{
        font-size: 32px;
    }
    
    .sobre-nos-content p{
        font-size: 17px;
    }

    .upper-banner h1{
        font-size: 50px;
    }

    .banner {
        width: 400px;
    }

    .banner p {
        font-size: 17px;
    }

    .banner h1 {
        font-size: 30px;
    }

    #precos {
        margin-top: 100px;
   }
}

@media (max-width: 500px) {
    .banner {
        width: 300px;
        height: 450px;
    }

    .banner p {
        font-size: 14px;
    }

    .banner h1 {
        font-size: 28px;
    }
